export default function microCard() {
    document.querySelectorAll(".read-more-link").forEach((button) => {
        button.addEventListener("click", function (event) {
            event.preventDefault();
            const postItem = button.closest(".gh-micro-card");
            const contentSection = postItem.querySelector(".e-content");

            if (postItem.classList.contains("expanded")) {
                // İçerik genişletilmişse daralt
                postItem.classList.remove("expanded");
                contentSection.style.maxHeight = "6em"; // Excerpt yüksekliğine ayarla
                button.textContent = "Show more"; // Buton metnini 'Show more' yap
            } else {
                // İçerik daraltılmışsa genişlet
                postItem.classList.add("expanded");
                contentSection.style.maxHeight = "none"; // İçeriğin tamamını göster
                button.textContent = "Show less"; // Buton metnini 'Show less' yap
            }
        });
    });
}
